import React, { Component } from 'react';
import Code from '../../comp/Code';

export default class AboutTree extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div>
                <p className="article-title"><span className="textShadow"></span></p>
                <div className="article-desc">
                    <br />
                    <Code code={`
function Node(data, left, right) {
    this.data = data;
    this.left = left;
    this.right = right;
}

/**
 *  
 * 
 */
function BST() {
    this.root = null;
    this.insert = insert;
    this.isSymmetry = isSymmetry;
    this.inOrder = inOrder;
    this.preOrder = preOrder;
    this.postOrder = postOrder;
    this.getMin = getMin;
    this.getMax = getMax;
    this.find = find;
}

function insert(data) {
    var node = new Node(data, null, null);
    if (this.root === null) {
        this.root = node;
    } else {
        var current = this.root;
        var parent = null;
        while(true) {
            parent = current;
            if (data < current.data) {
                current = current.left;
                if (current == null) {
                    parent.left = node;
                    break;
                }
            } else {
                current = current.right;
                if (current == null) {
                    parent.right = node;
                    break;
                }
            }
        }
    }
}

function isSymmetry() {
    var root = this.root;
    if (root == null) {
        return true;
    }
    return compare(root.left, root.right);
}

function compare(left,right) {
    if (left == null && right == null) {
        return true;
    }
    if (left == null || right == null) {
        return false;
    }
    if (left.data !== right.data) {
        return false;
    }
    return compare(left.left, left.right) && compare(right.left, right.right);
}
// 
//  --->  ---> 

function inOrder(node) {
    if (node != null) {
        inOrder(node.left);
        console.log(node);
        inOrder(node.right);
    }
}

// 
//  --->  ---> 
function preOrder(node) {
    if (node != null) {
        console.log(node);
        preOrder(node.left);
        preOrder(node.right);
    }
}

// 
//  --->  ---> 
function postOrder(node) {
    if (node != null) {
        postOrder(node.left);
        postOrder(node.right);
        console.log(node);
    }
}


// 
// 

function getMin() {
    var currentRoot = this.root;
    while(currentRoot.left != null) {
        currentRoot = currentRoot.left;
    }
    return currentRoot.data;
}

// 
// 
function getMax() {
    var currentRoot = this.root;
    while(currentRoot.right != null) {
        currentRoot = currentRoot.right;
    }
    return currentRoot.data;
}

// 
// 
function find(data) {
    var currentRoot = this.root;
    while(currentRoot != null) {
        if (currentRoot.data == data) {
            return currentRoot;
        } else if (currentRoot.data > data) {
            currentRoot = currentRoot.left;
        } else {
            currentRoot = currentRoot.right;
        }
    }
    return null;
}`} />
                </div>
            </div>
        )
    }
}