import React, { Component } from 'react';
import Code from '../../comp/Code';

export default class MyCache extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div>
                <p className="article-title"><span className="textShadow"></span></p>
                <div className="article-desc">
                    
                    <Code code={`
function add() {
    const args = Array.from(arguments);
    return args.reduce((preV, curV) => {
        return preV + curV;
    });
}
function memorize(fn) {
    let cache = {};
    return function() {
        console.time('------');
        const args = Array.from(arguments);
        const key = JSON.stringify(args);
        if (!cache[key]) {
            const res = fn.apply(null, args);
            cache[key] = res;
            console.timeEnd('------');
            return res;
        } else {
            console.timeEnd('------');
            return cache[key];
        }
    }
}

const adder = memorize(add);
adder(1,2,3);


//Map
function memorize(fn) {
    let cache = new Map();
    return function() {
        console.time('------');
        const args = Array.from(arguments);
        const key = JSON.stringify(args);
        if (!cache.has(key)) {
            const res = fn.apply(null, args);
            cache.set(key, res);
            console.timeEnd('------');
            return res;
        } else {
            console.timeEnd('------');
            return cache.get(key);
        }
    }
}
`} />
                </div>
            </div>
        )
    }
}