import React, { Component } from 'react';
import Code from '../../comp/Code';

export default class MyBind extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div>
                <p className="article-title"><span className="textShadow">bind</span></p>
                <div className="article-desc">
                    <Code code={`
/**
 *bind()  bind() 
 *	 this  bind() 
 *	: function.bind(thisArg, arg1, arg2, ...)
 *
 *
 */
 
 /**
 *
 *	1. bind()thisbind()
 *	2. newthisbindnewthis
 *	3. 
 */
 
 Function.prototype.newBind = function(context, ...args) {
     var self = this
     // new
     var _bind = function () {
         //thisselfthisnew
         self.apply(this instanceof self ? this : context, args.concat(Array.from(arguments)));
     }
     // 
     _bind.prototype = Object.create(self.prototype);
     return _bind;
 }
 
 //
 const obj = { name: 'hello world' };
 function foo() {
     console.log(this.name);
 }
 
 foo.newBind(obj)();    //hello world`} />
                </div>
            </div>
        )
    }
}