import React, { Component } from 'react';
import Code from '../../comp/Code';

export default class MyLet extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div>
                <p className="article-title"><span className="textShadow">let</span></p>
                <div className="article-desc">
                    <Code code={`
//letlet

for (let i = 0; i < 5; i++) {
    console.log(i);
}
console.log(i);  // Uncaught ReferenceError: i is not defined

//es5varlet
(function() {
    for (var i = 0; i < 5; i++) {
        console.log(i);
    }
})();
console.log(i);  // Uncaught ReferenceError: i is not defined


//es5var
for (var i = 0; i < 5; i++) {
    console.log(i);
}
console.log(i); // 5`} />
                </div>
            </div>
        )
    }
}