

import React, { Component } from 'react';
import Code from '../../comp/Code';

export default class MyPromise extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div>
                <p className="article-title"><span className="textShadow">promise</span></p>
                <div className="article-desc">
                    <Code code={`
/*
	 https://juejin.im/post/5e3b9ae26fb9a07ca714a5cc
*/
const PENDING = 'pending';
const FULFILLED = 'fulfilled';
const REJECTED = 'rejected';

class MyPromise {
	constructor(executor) {
		this._state = PENDING;   //promise
		this._resolveQueue = [];
		this._rejectQueue = [];

		let _resolve = (value) => {
			if (this._state !== PENDING) {
				return;
			}
			this._state = FULFILLED;

			while (this._resolveQueue.length) {
				const callback = this._resolveQueue.shift();
				callback(value);
			}
		}

		let _reject = (value) => {
			if (this._state !== PENDING) {
				return;
			}
			this._state = REJECTED;

			while (this._rejectQueue.length) {
				const callback = this._rejectQueue.shift();
				callback(value);
			}
		}
	}

	/*
		@then
		.then()PromisethenthenPromise
		.then().then()
		.then()Promisethenthen
	*/

	then(resolveFn, rejectFn) {
		//promise
		return new MyPromise((resolve, reject) => {
			const fulfilledFn = value => {
				try {
					let x = resolveFn(value);
					x instanceof MyPromise ? x.then(resolve, reject) : resolve(x);
				} catch (err) {
					reject(err);
				}
			};
			this._resolveQueue.push(fulfilledFn);

			cosnt rejectedFn = value => {
				try {
					let x = rejectFn(value);
					x instanceof MyPromise ? x.then(resolve, reject) : resolve(x);
				} catch (err) {
					reject(err);
				}
			};
			this._rejectQueue.push(rejectedFn);
		})
	}
}`} />
                </div>
            </div>
        )
    }
}