import React, { Component } from 'react';
import Code from './../../comp/Code';

export default class SetState extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 0,
        }
    }

    componentDidMount() {
        this.setState({
            value: this.state.value + 1,
        });
        console.log('---componentDidMount---', this.state.value);
        this.setState({
            value: this.state.value + 2,
        });
        this.setState({
            value: this.state.value + 3,
        });
        this.setState({
            value: this.state.value + 4,
        });

        document.body.addEventListener('click', () => {
            this.setState({
                value: this.state.value + 1,
            });
            console.log('---addEventListener---', this.state.value);
        }, false);

        setTimeout(() => {
            this.setState({
                value: this.state.value + 1,
            });
            console.log('---setTimeout---', this.state.value);
        }, 3000);

        // setInterval(() => {
        //     this.setState({
        //         value: this.state.value + 1,
        //     });
        //     console.log('---setInterval---', this.state.value);
        // }, 4000);
    }

    btnClick() {
        this.setState({
            value: this.state.value + 1,
        });
        console.log('------', this.state.value);
    }

    render() {
        return (
            <div>
                <p className="article-title"><span className="textShadow">setState</span></p>
                <div className="article-desc">
                    1. setState<br/><br/>
                    2. setStatesetTimeout<br/><br/>
                    3. setStatesetStatecallback<br/><br/>
                    <button onClick={() => this.btnClick()}></button><br /><br />
                
                    <span className="mark">setTimeoutsetState</span><br />
                    setTimeoutevent loopsetTimeoutsetState<br/><br/>
                    reactreactjsxonClickonChange
                </div>
            </div>
        )
    }
}